@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff"), url("../fonts/Montserrat-SemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff"), url("../fonts/Montserrat-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
  border: none;
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: inherit;
}

ul,
ol,
li {
  list-style: none;
}

li {
  position: relative;
}

img {
  vertical-align: top;
  width: 100%;
  height: 100%;
}

button {
  cursor: pointer;
  background-color: unset;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

:focus {
  outline: none;
}

html {
  height: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

body {
  font-family: "Montserrat", "Arial", "Helvetica", sans-serif;
  min-width: 320px;
  height: 100%;
  max-width: 100%;
  font-weight: 400;
  font-style: normal;
  overflow-x: hidden;
  padding-top: 120px;
  padding-bottom: 120px;
}
@media (min-width: 768px) {
  body {
    padding-top: 200px;
    padding-bottom: 200px;
  }
}

body.lock,
html.lock {
  overflow: hidden;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
}

.wrapper {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

.title {
  color: #0F348C;
  font-weight: 700;
}
.title--basic {
  font-size: 22px;
  text-align: center;
  line-height: 1.36;
}
@media (min-width: 400px) {
  .title--basic {
    font-size: 28px;
  }
}
@media (min-width: 768px) {
  .title--basic {
    font-size: 34px;
    line-height: 1.3;
  }
}
.title span {
  color: #FE5339;
}

.button {
  position: relative;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  padding: 10px;
  width: 100%;
  max-width: 294px;
  min-height: 62px;
  background-color: #FFDD2D;
  border-radius: 10px;
  color: #222222;
  font-weight: 600;
  font-size: 18px;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .button {
    margin: 0;
    min-height: 64px;
  }
  .button:hover {
    top: -2px;
    box-shadow: 0px 8px 10px 0px rgba(0, 0, 0, 0.2470588235);
  }
  .button:active {
    top: 0;
    box-shadow: none;
  }
}

.first-screen {
  position: relative;
}
@media (min-width: 810px) {
  .first-screen {
    max-width: 1045px;
    margin: 0 auto;
  }
}
.first-screen--bottom-indent {
  margin-bottom: 22px;
}
@media (min-width: 550px) {
  .first-screen--bottom-indent {
    margin-bottom: 70px;
  }
}
@media (min-width: 810px) {
  .first-screen--bottom-indent {
    margin-bottom: 151px;
  }
}
.first-screen__title {
  margin-bottom: 16px;
  line-height: 1.36;
  font-size: 22px;
}
@media (min-width: 400px) {
  .first-screen__title {
    margin-bottom: 30px;
    font-size: 28px;
  }
}
@media (min-width: 550px) {
  .first-screen__title {
    font-size: 38px;
  }
}
@media (min-width: 810px) {
  .first-screen__title {
    font-size: 38px;
    line-height: 1.26;
    margin-bottom: 17.5px;
  }
}
.first-screen__subtitle {
  margin-bottom: 32px;
  color: #0F348C;
  font-size: 12px;
  line-height: 1.5;
}
@media (min-width: 400px) {
  .first-screen__subtitle {
    margin-bottom: 50px;
    font-size: 18px;
  }
}
@media (min-width: 810px) {
  .first-screen__subtitle {
    margin-bottom: 40px;
    font-size: 20px;
    line-height: 1.6;
    color: #000;
  }
}
@media (min-width: 810px) {
  .first-screen__title, .first-screen__subtitle {
    max-width: 491px;
    width: 63%;
  }
}
.first-screen__img {
  position: relative;
  display: block;
  max-height: 340px;
  height: 100%;
  width: 105%;
  margin-left: -7px;
  margin-bottom: 50px;
}
@media (min-width: 550px) {
  .first-screen__img {
    margin-bottom: 60px;
  }
}
@media (min-width: 810px) {
  .first-screen__img {
    position: absolute;
    top: 0;
    right: 0;
    max-width: 364px;
    width: 35.7%;
    height: auto;
    margin: 0;
  }
}
.first-screen__img img {
  display: block;
  height: 100%;
  width: 100%;
  max-height: inherit;
  object-fit: contain;
  object-position: center top;
}
.first-screen__button {
  font-size: 16px;
}
@media (min-width: 400px) {
  .first-screen__button {
    font-size: 18px;
  }
}
@media (min-width: 768px) and (max-width: 809px) {
  .first-screen__button {
    margin: 0 auto;
  }
}

.triggers {
  padding-top: 82px;
  background-image: url("../img/svg/pixels-1.svg");
  background-repeat: no-repeat;
  background-position: center top;
}
@media (min-width: 768px) {
  .triggers {
    padding-top: 0;
    max-width: 1035px;
    margin: 0 auto;
    background-image: none;
  }
}
.triggers--bottom-indent {
  margin-bottom: 54px;
}
@media (min-width: 768px) {
  .triggers--bottom-indent {
    margin-bottom: 109px;
  }
}
.triggers__title {
  margin-bottom: 64px;
}
.triggers__list {
  margin-bottom: 64px;
}
@media (min-width: 550px) {
  .triggers__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .triggers__list {
    margin-bottom: 57px;
  }
}
@media (min-width: 1024px) {
  .triggers__list {
    margin-left: 71px;
  }
}
.triggers__item:nth-child(n+2) {
  margin-top: 48px;
}
@media (min-width: 550px) {
  .triggers__item:nth-child(n+2) {
    margin-top: 0;
  }
}
@media (min-width: 550px) {
  .triggers__item:nth-child(1), .triggers__item:nth-child(2) {
    margin-bottom: 80px;
  }
}
.triggers__description {
  margin-bottom: 27px;
  font-size: 15px;
  font-weight: 700;
  line-height: 1.39;
  text-align: center;
  color: #428ECC;
}
@media (min-width: 550px) {
  .triggers__description {
    display: none;
  }
}
.triggers__button {
  margin: 0 auto;
}

@media (min-width: 550px) {
  .trigger {
    display: flex;
    max-width: 438px;
    width: calc(50% - 7.5px);
  }
  .trigger:nth-child(2n+1) {
    margin-right: 15px;
  }
}
.trigger__icon {
  max-height: 40px;
  max-width: 40px;
  margin-bottom: 11.6px;
  flex-shrink: 0;
}
.trigger__icon svg {
  width: 100%;
  height: 100%;
  fill: #428ECC;
}
@media (min-width: 550px) {
  .trigger__icon {
    margin-right: 15px;
  }
}
@media (min-width: 768px) {
  .trigger__icon {
    margin-bottom: 0;
    margin-right: 30px;
    max-width: 50px;
    max-height: 50px;
  }
  .trigger__icon--star svg {
    width: 49px;
  }
  .trigger__icon--shield svg {
    width: 50px;
  }
  .trigger__icon--like svg {
    width: 48px;
  }
  .trigger__icon--edit svg {
    width: 49px;
  }
}
.trigger__title {
  margin-bottom: 10px;
  color: #428ECC;
  font-size: 20px;
  line-height: 1.3;
  font-weight: 600;
}
@media (min-width: 768px) {
  .trigger__title {
    margin-bottom: 15px;
    font-size: 28px;
  }
}
.trigger__description {
  color: #242424;
  line-height: 1.56;
}

.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  display: flex !important;
  justify-content: center;
  align-items: center;
  width: 39px;
  height: 39px;
  background: rgba(63, 141, 203, 0.56);
  border-radius: 50%;
}
.arrow.slick-disabled {
  display: none !important;
}
.arrow--next {
  right: 10px;
}
.arrow--next svg {
  margin-left: 3px;
}
.arrow--prev {
  left: 10px;
}
.arrow--prev svg {
  transform: rotate(180deg);
}

.cost {
  padding-top: 71px;
  background-image: url("../img/svg/pixels-5.svg"), url("../img/svg/pixels-6.svg");
  background-repeat: no-repeat;
  background-position: left 10px top 20px, right top;
}
@media (min-width: 450px) {
  .cost {
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (min-width: 930px) {
  .cost {
    display: flex;
    padding-top: 0;
    justify-content: space-between;
    max-width: 1193px;
    margin: 0 auto;
    background-image: none;
  }
  .cost__container {
    max-width: 299px;
    padding-top: 20px;
    margin-right: 20px;
  }
}
.cost__title {
  margin-bottom: 36px;
  max-width: 90%;
}
@media (min-width: 930px) {
  .cost__title {
    margin-bottom: 20px;
    max-width: none;
    padding: 0;
    text-align: left;
    line-height: 1.2;
  }
}
.cost__description {
  margin-bottom: 32px;
  color: #0F348C;
  text-align: center;
  line-height: 1.56;
}
@media (min-width: 930px) {
  .cost__description {
    margin-bottom: 23px;
    padding: 0;
    text-align: left;
    line-height: 1.22;
    color: #000;
  }
}
.cost__table {
  margin-bottom: 34px;
  box-shadow: 0px 8px 10px 0px rgba(107, 139, 217, 0.1019607843);
  border-radius: 26px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 768px) {
  .cost__table {
    max-width: 761px;
  }
}
@media (min-width: 930px) {
  .cost__table {
    margin-left: 0;
    margin-right: 0;
  }
}
.cost__table-title {
  background: #EBF4FD;
  border-radius: 26px 26px 0px 0px;
  padding: 27px 20px 26px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.22;
}
@media (min-width: 768px) {
  .cost__table-title {
    padding: 34px 169px 29px 64px;
    font-size: 22px;
  }
}
.cost__content {
  padding: 16px 20px 35px;
}
@media (min-width: 768px) {
  .cost__content {
    padding: 13px 50px 26px 64px;
  }
}
@media (min-width: 930px) {
  .cost__buttons {
    display: none;
  }
}
@media (min-width: 768px) and ((max-width: 930px)) {
  .cost__button {
    margin: 0 auto;
  }
}
.cost__button--desktop {
  display: none;
}
@media (min-width: 930px) {
  .cost__button--desktop {
    display: flex;
  }
}
.cost table {
  border-collapse: collapse;
  width: 100%;
}
.cost table th {
  padding: 12px 0 14px;
  font-weight: 600;
}
.cost table th:first-child {
  padding-right: 24px;
}
@media (min-width: 768px) {
  .cost table th {
    padding: 17px 0 14px;
  }
}
.cost table thead th {
  font-size: 15px;
  line-height: 1.3;
  text-align: left;
  color: #0F348C;
}
@media (min-width: 768px) {
  .cost table thead th {
    font-size: 20px;
  }
}
.cost table tbody th:first-child {
  width: 60%;
  color: #242424;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.69;
  text-align: left;
}
@media (min-width: 768px) {
  .cost table tbody th:first-child {
    font-size: 16px;
    line-height: 1.375;
  }
}
.cost table tbody th:last-child {
  color: #0F348C;
  font-size: 20px;
  line-height: 1.1;
}
@media (min-width: 768px) {
  .cost table tbody th:last-child {
    font-size: 22px;
    line-height: 1;
  }
}
.cost table tbody tr {
  border-bottom: 1px solid #EFEFEF;
}
.cost table tbody tr:last-child {
  border-bottom: none;
}